@import "~styles/utils/_variables";
@import "~styles/utils/_mixins";

// 
body {
    font-family: $font-primary;
    font-size: 14px;
    padding: 0;
    background: white;
}

*,
*:after,
*:before {
    box-sizing: border-box;
}

.wrapper {
    max-width: 1160px;
    margin: 0 auto;
    padding: 20px;
}

.wrapper-header,
.wrapper-footer {
    max-width: 1184px;
    margin: 0 auto;
}

//    ,   

//  
.bold {
    font-weight: 700;
}

//  
.semi-bold {
    font-weight: 600;
}

//  
.normal {
    font-weight: normal;
}

//  
.light {
    font-weight: 300;
}

.w-100 {
    width: 100%;
}

.w-75 {
    width: 75%;
}

.w-50 {
    width: 50%;
}

.w-25 {
    width: 25%;
}

.ta-c {
    text-align: center;
}

//   body   
.disable-scroll {
    overflow: hidden;
}

.disable-transition {
    * {
        transition: unset !important;
    }
}

//       
@media screen and (min-width: $desctop) {
    .touch-only {
        display: none;
    }
}
